



















import Vue from "vue";
import api from "../service/api";
import { Contacts } from "../service/interfaces";
export default Vue.extend({
  name: "contacts",
  data() {
    return {
      model: <Contacts>{},
    };
  },
  methods: {
    async submit() {
      try {
        await api.post("/contact", this.model);
      } catch (err) {
        console.error(err);
      }
    },
  },
  computed: {},
});
